.app {
  display: flex;
  flex-direction: row;
  padding: 30px;
  background-image: url(./images/background.png);
  background-position: center center;
  background-repeat: no-repeat;

  width: 95vw;
  height: 95vh;
  overflow: -moz-hidden-unscrollable;
}

.game {
  width: 560px;
  height: 560px;
  display: flex;
  flex-wrap: wrap;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 20px;
  margin: auto;
}

.game img {
  width: 70px;
  height: 70px;
}

.display {
  background-color: rgb(248, 163, 191);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  color: purple;
  width: 120px;
  height: 100px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 3px 3px rgb(212, 158, 177);
  margin: auto;
}

@media screen and (max-width: 500px) {
  .app {
    display: flex;
    flex-direction: column;
    padding: 15px;
    width: 90vw;
    height: 90vh;
  }

  .display {
    width: 50px;
    height: 50px;
  }

  .game img {
    width: 40px;
    height: 40px;
  }

  .game {
    width: 320px;
    height: 320px;
  }
}

@media screen and (max-width: 350px) {
  .app {
    display: flex;
    flex-direction: column;
    padding: 15px;
    width: 85vw;
    height: 85vh;
  }

  .display {
    width: 40px;
    height: 40px;
  }

  .game img {
    width: 35px;
    height: 35px;
  }

  .game {
    width: 280px;
    height: 280px;
  }
}
